<template>
	<div class="user-container">
		<div class="header">
			<div class="header-user">
				<div class="header-media">
					<img class="user-avatar" :src="userInfo.avatarUrl" alt="">
				</div>
				<div class="header-title">
					{{userInfo.nick}}
				</div>
			</div>
			<div class="header-setting" @click="toSetting">
				<van-icon name="arrow" />
			</div>
		</div>

		<div class="main">
			<!-- 全部订单 -->
			<div class="group balance-group">
				<van-cell value="我的钱包" />
				<van-row class="group-inner">
					<van-col span="6">
						<div class="balance-group-title">{{balance}}</div>
						<div class="balance-group-txt">可用余额(元)</div>
					</van-col>
					<van-col span="6">
						<div class="balance-group-title">{{freeze}}</div>
						<div class="balance-group-txt">冻结金额(元)</div>
					</van-col>
					<van-col span="6">
						<div class="balance-group-title">{{score}}</div>
						<div class="balance-group-txt">剩余积分(元)</div>
					</van-col>
					<van-col span="6">
						<div class="balance-group-title">{{totleConsumed}}</div>
						<div class="balance-group-txt">累计消费(元)</div>
					</van-col>
				</van-row>
			</div>
			<div class="group balance-group">
				<van-cell value="全部订单" is-link to="/order-list" />
				<van-row class="group-inner">
					<van-col offset="2" span="4">
						<router-link to="/order-list?status=0">
							<div class="balance-group-title">
								<van-icon name="pending-payment" :badge="orderCount.count_id_no_pay" />
							</div>
							<div class="balance-group-txt">待付款</div>
						</router-link>
					</van-col>
					<van-col span="4">
						<router-link to="/order-list?status=1">
							<div class="balance-group-title">
								<van-icon name="tosend" :badge="orderCount.count_id_no_transfer" />
							</div>
							<div class="balance-group-txt">待发货</div>
						</router-link>

					</van-col>
					<van-col span="4">
						<router-link to="/order-list?status=2">
							<div class="balance-group-title">
								<van-icon name="logistics" :badge="orderCount.count_id_no_confirm" />
							</div>
							<div class="balance-group-txt">待收货</div>
						</router-link>
					</van-col>
					<van-col span="4">
						<router-link to="/order-list?status=3">
							<div class="balance-group-title">
								<van-icon name="comment-o" :badge="orderCount.count_id_no_reputation" />
							</div>
							<div class="balance-group-txt">评价</div>
						</router-link>
					</van-col>
					<van-col span="4">
						<router-link to="/order-list">
							<div class="balance-group-title">
								<van-icon name="after-sale" />
							</div>
							<div class="balance-group-txt">退款/售后</div>
						</router-link>
					</van-col>
				</van-row>
			</div>

			<!-- 常用 -->
			<div class="group balance-group">
				<van-cell value="常用" />
				<van-row class="group-inner">
					<van-grid :column-num="4">
						<van-grid-item v-for="(item,index) in tools" :key="index" :icon="item.icon" :text="item.title" :to="item.path" />
					</van-grid>
				</van-row>
			</div>

			<!-- 关于，日志 -->
			<div class="group balance-group">
				<van-cell value="关于" is-link @click="onShowAbout"></van-cell>
				<van-cell value="日志" is-link></van-cell>
			</div>

		</div>

		<!-- 底部栏开始 -->
		<van-tabbar route v-model="active" active-color="#ff6700">
			<van-tabbar-item replace to="/home" icon="home-o">
				首页
			</van-tabbar-item>
			<van-tabbar-item replace to="/cart" icon="cart-o">
				购物车
			</van-tabbar-item>
			<van-tabbar-item replace to="/user" icon="manager-o">
				我的
			</van-tabbar-item>
		</van-tabbar>
		<!-- 底部栏结束s -->
	</div>
</template>

<script>
	import util from '@/common/util/util'
	export default {
		data() {
			return {
				active: 3,
				userInfo: {},
				balance: 0,
				freeze: 0,
				score: 0,
				totleConsumed: 0,
				orderCount: {}, // 订单统计
				tools: [{
						icon: 'balance-o',
						title: '资金明细',
						path: '/cashlog'
					},
					{
						icon: 'star-o',
						title: '我的收藏',
						path: '/favgoods'
					},
					// {icon:'comment-o',title:'我的评价',path:'/favgoods'},
					{
						icon: 'coupon-o',
						title: '优惠券',
						path: '/coupon'
					},
					{
						icon: 'location-o',
						title: '收货地址',
						path: '/address-list'
					},
					{
						icon: 'setting-o',
						title: '设置',
						path: '/setting'
					}
				]
			};
		},
		created() {
			this.getUserDetail();
			this.getUserAmount();
			this.getOrderList();
		},
		methods: {
			getUserAmount() {
				this.$request.get('/user/amount', {
					token: util.storage.get('token')
				}).then(res => {
					console.log('账户余额', res);
					this.balance = res.data.balance;
					this.freeze = res.data.freeze;
					this.score = res.data.score;
					this.totleConsumed = res.data.totleConsumed;
				})
			},
			getUserDetail() {
				this.$request.get('/user/detail', {
					token: util.storage.get('token')
				}).then(res => {
					console.log('用户详情', res);
					if (res.code !== 0) {
						return;
					}
					this.userInfo = res.data.base;
				})
			},
			getOrderList() {
				this.$request.post('/order/statistics', {
					token: util.storage.get('token'),
				}).then(res => {
					console.log('用户订单', res);
					if (res.code !== 0) {
						return;
					}
					this.orderCount = res.data;
				});
			},
			onShowAbout() {
				this.$dialog.alert({
					// title: '标题',
					message: this.$store.state.globalData.desc
				}).then(() => {
					// on close
				})
			},
			toSetting() {
				this.$router.push({
					path: '/setting'
				});
			}
		}
	}
</script>

<style lang="less" scoped>
	.style-box() {
		box-sizing: border-box;
		border-radius: 8px;
		overflow: hidden;
		background: #fff;
	}

	.user-container {
		width: 100%;
		height: 100vh;
	}

	.header {
		box-sizing: border-box;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 180px;
		color: #fff;
		padding: 0 15px;
		// background-color: #fe4140;
		background: linear-gradient(309deg,
			rgba(255, 112, 61, 1) 0%,
			rgba(255, 70, 57, 1) 100%);

		&-user {
			display: flex;
			align-items: center;
		}

		&-media {
			width: 80px;
			height: 80px;
			border-radius: 50%;
			overflow: hidden;
			margin-right: 20px;
			// float: left;

			.user-avatar {
				width: 100%;
				height: 100%;
			}
		}

		&-title {
			// float: left;
			font-size: 22px;
		}

		&-setting {
			// position: absolute;
			// top: 30px;
			// right: 30px;
			font-size: 22px;
			// float: right;
			// margin-left: 45vw;
		}
	}

	.main {
		width: 100%;
		height: 100%;
		// padding:0 20px;
		margin-top: -50px;
		// background-color: ;
		background-color: rgba(0, 0, 0, .03);
	}

	.group {
		.style-box();
		margin: 15px 15px 15px 15px;

		&-inner {
			padding: 10px 0;
		}
	}

	.balance-group {
		.style-box();

		.van-row {
			text-align: center;
		}

		.van-col {
			text-align: center;
		}

		&-title {
			font-size: 24px;
			font-weight: bold;
			color: #000;
			margin-bottom: 5px;
			margin-top: 10px;
			// overflow: hidden;
		}

		&-txt {
			font-size: 12px;
			// color: #999;
		}
	}

	.changy-group {
		.van-icon {
			font-size: 24px;
		}

		.van-col {
			margin-bottom: 10px;
		}
	}

	.user-gruop {
		&-name {
			// padding: 10px 0;
			color: #333;
			font-size: 12px;
			margin-bottom: 10px;
		}
	}

	.user-section {
		.style-box();
		margin: 0 20px 15px 20px;

		&-title {
			font-size: 14px;
			color: #000;
			font-weight: bold;
		}

		&-hd {
			box-sizing: border-box;
			width: 100%;
			padding: 10px 0px;
			overflow: hidden;
			color: #323233;
			font-size: 14px;
			line-height: 24px;
			background-color: #fff;
			display: flex;
			align-items: center;
		}

		&-bd {
			box-sizing: border-box;
			width: 100%;
			padding: 15px 0;
		}

		&-grid {
			box-sizing: border-box;
			width: 20%;
			float: left;
			text-align: center;

			.van-icon {
				font-size: 24px;
				margin-bottom: 5px;
			}

			&-img {
				display: inline-block;
				width: 30px;
				height: 30px;
				// margin-bottom: 5px;
			}

			&-title {
				color: #333;
				font-size: 12px;
			}
		}
	}
</style>
